html {
  background-color: #e3e3e1;
  text-align: center;
}

.App {
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
  font-family: 'American Typewriter', Courier, monospace;
}

h1 {
  width: 95%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 0.2em;
  padding-bottom: 0.2em;
  font-family: 'American Typewriter', Courier, monospace;
  font-size: 2.2em;
  font-weight: 800;
}

select {
  height: 3em;
  width: 100%;
  border-radius: 5px;
  text-align: center;
  text-wrap: auto;
  font-family: 'American Typewriter', Courier, monospace;
  font-size: 0.8em;
  font-weight: 500;
  background-color: white;
  color: black;
}

.selector {
  display: inline-flex;
  margin-left: auto;
  margin-right: auto;
  max-width: 22em;
  margin-left: auto;
  margin-right: auto;
  margin-top:0.2em;
  margin-bottom:0.2em;
  padding: 1em 0.5em 1em 0.5em;
  border-radius: 10px;
  background-color: rgb(6, 125, 172);
}

.column {
  display: block;
  width: 100%;
  margin-left: 0.5em;
  margin-right: 0.5em;
}

.start-end {
  background-color: black;
  color: white;
  padding-top: 10px;
  padding-bottom: 10px;
  max-width: 23em;
  margin-left: auto;
  margin-right: auto;
  border-radius: 10px;
  font-weight: 500;
}


.winning-modal {
  position: absolute;
  background-color: white;
  margin-left: auto;
  margin-right: auto;
  left: 50%;
  top: 5em;
  margin-left: -11em;
  width: 20em;
  padding: 0 1em 1em 1em;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.winning-modal p {
  font-size: 0.8em !important;
}